import React, { useState } from 'react';
import Header from './components/Header'
import Input from './components/Input'
import Output from './components/Output';
import Button from './components/Button';


function App() {
const [input,setInput] = useState(0);
const [output,setOutput] = useState(0);
const  handleInput=(value)=>{
    
    switch (value) {
  case 'AC':
      setInput(0);
      setOutput(0)
    break;
  case 'Del':
  
    if(input.length>1){
    setInput(input.slice(0,-1))
    }else{
      setInput(0);
    }
    
  break;
  case '=':
    if(input.includes('1500')){
      setOutput('LYSM Thae  ')
      return;
    }
    try{
      const res=eval(input);
      setOutput(res);
      setInput(0)
    }catch(e){
      console.log(e)
    }
   // setOutput('Use calculator on your phone.All i know is about Thae')
  
    break;
  default:
    setInput(input===0?value:`${input}${value}`)
    break;
  }
}
return (
  <div className='bg-zinc-700 w-full min-h-screen'>
    <Header/>
    <div className= ' bg-zinc-500 mb-5 mt-8 rounded-lg border  p-3 mx-2 ' >
    <Output output={output}/>
    <Input input={input} />
    </div>

    <Button handleInput={handleInput}/>
  </div>
)
}

export default App