import React from 'react'

function Button({handleInput}) {
    const btns=['AC','Del','%','/',7,8,9,'*',4,5,6,'-',1,2,3,'+','00',0,'.','=']
  return (
    <div className='grid grid-cols-4 mx-1'>
        {btns.map((e,index)=>(
            <button key={index} className={`text-white border rounded-lg m-1 hover:bg-black text-xl transition duration-100 p-3 py-5 text-center ${e === '=' ? 'bg-amber-700' : 'bg-gray-900'}`}
            onClick={()=>handleInput(e)}
            
            >{e}</button>
            ))}
    </div>
  )
}

export default Button